import React from "react";
import styled from "styled-components";

const ButtonContainer = styled.button`
	@keyframes shine {
		0% {
			background-position:-5000px 0;
		}
		to {
			background-position:5000px 0;
		}
	}
	font-family: "Avenir-Medium";
	font-weight: 700;
	border: 0;
	border-radius: 8px;
	cursor: pointer;
	display: inline-block;
	line-height: 1;
	outline: none;
	text-transform: ${({ textTransform }) => `${textTransform}`};
	svg {
		width: 12px;
		height: 7px;
		margin-left: 6px;
	}
	&:disabled {
		opacity: 0.5;
		cursor: not-allowed;
	}
	&.storybook-button--primary {
		color: #fff;
		background-color: #4066ff;
		&:hover:not(:disabled) {
			background-color: #2947bf;
		}
		&.button-animated {
			animation: shine 200s linear infinite;
			background: linear-gradient(
				121.19deg,
				rgba(133,239,49,0) 25.73%,
				hsla(0,0%,100%,.3) 45.27%,
				rgba(133,239,49,0) 62.27%
			),#4066ff;
		}
	}
	&.storybook-button--launch {
		color: #fff;
		background-color: #765dca;
		&:hover:not(:disabled) {
			background-color: #9e83f7;
		}
		&.button-animated {
			animation: shine 200s linear infinite;
			background: linear-gradient(
				121.19deg,
				rgba(133,239,49,0) 25.73%,
				hsla(0,0%,100%,.3) 45.27%,
				rgba(133,239,49,0) 62.27%
			),#765dca;
		}
	}
	&.storybook-button--secondary {
		color: #4066ff;
		background-color: #fff;
		border: 1px solid #4066ff;
		&:hover:not(:disabled) {
			background-color: #ecf0ff;
		}
		&.button-animated {
			animation: shine 200s linear infinite;
			background: linear-gradient(
				121.19deg,
				rgba(133,239,49,0) 25.73%,
				hsla(0,0%,100%,.3) 45.27%,
				rgba(133,239,49,0) 62.27%
			),#ecf0ff;
		}
	}
	&.storybook-button--default {
		color: #000;
		background-color: #f4f6ff;
		&:hover:not(:disabled) {
			background-color: #ecf0ff;
		}
		&.button-animated {
			animation: shine 200s linear infinite;
			background: linear-gradient(
				121.19deg,
				rgba(133,239,49,0) 25.73%,
				hsla(0,0%,100%,.3) 45.27%,
				rgba(133,239,49,0) 62.27%
			),#ecf0ff;
		}
	}
	&.storybook-button--small {
		font-size: 12px;
		padding: 10px 16px;
	}
	&.storybook-button--medium {
		font-size: 14px;
		padding: 11px 20px;
	}
	&.storybook-button--large {
		font-size: 16px;
		padding: 20px 27px;
	}
	&.storybook-button--radius {
		border-radius: 28px;
		// padding: 11px 56px;
	}
`;

export interface IButtonProps {
	variant?: "primary" | "secondary" | "launch" | "default";
	disable?: boolean;
	type?: "button" | "submit";
	isRadius?: boolean;
	size?: "small" | "medium" | "large";
	onClick?: any;
	textTransform?: "upperCase" | "lowerCase" | "capitalize" | "none";
	disabled?: boolean;
	children: any;
	addedClassName?: string;
	isAnimated?: boolean;
}

const Button: React.FC<IButtonProps> = ({
	disable = false,
	textTransform = "none",
	variant = "primary",
	size = "medium",
	isRadius = false,
	children = "Click",
	addedClassName = "",
	type = "button",
	isAnimated = false,
	...props
}) => {
	const setRadius = isRadius
		? "storybook-button--radius"
		: "storybook-button--non-radius";
	const animatedClass = isAnimated
		? "button-animated" : ''

	return (
		<ButtonContainer
			textTransform={textTransform}
			type={type}
			disabled={disable}
			className={[
				"storybook-button",
				`storybook-button--${size}`,
				`storybook-button--${variant}`,
				`${animatedClass}`,
				`storybook-button--${textTransform}`,
				setRadius,
				addedClassName,
			].join(" ")}
			{...props}
		>
			{children}
		</ButtonContainer>
	);
};

export default Button;