import React, { useRef, useEffect, useState } from "react";
import styled from "styled-components";

const TooltipContainer = styled.div`
	position: relative;
	.tooltip__tip {
		position: absolute;
		z-index: 999;
		padding: 10px;
		border-radius: 10px;
		background: #000;
		color: #fff;
	}
`;

export interface ITooltipProps
	extends React.BaseHTMLAttributes<HTMLDivElement> {
	addedClassName?: string;
	position?: "top" | "left" | "right" | "bottom";
	text?: string;
	arrow?: boolean;
	children: any;
	content: any;
}
const Tooltip: React.FC<ITooltipProps> = ({
	addedClassName = "",
	position = "top",
	content = '1212',
	arrow = false,
	children
}) => {
	let timeout: any;
	const toolTipContainerRef: any = useRef();
	const toolTipRef: any = useRef();
	const [active, setActive] = useState(false);
	const [tooltipDirection, setTooltipDirection] = useState(position);

	const [tooltipPosition, setTooltipPosition] = useState({});

	useEffect(() => {
		const {
			top = 0,
			left = 0,
			bottom = 0,
			right = 0
		} = toolTipContainerRef.current.getBoundingClientRect();
		// if (top > bottom) {
		// 	setTooltipDirection("top");
		// 	console.log('top');
		// 	setTooltipPosition({ top: top - 50, left });
		// } else {
		// 	setTooltipDirection("bottom");
		// 	console.log('bottom');
		// 	setTooltipPosition({ top: top + 50, left });
		// }
	}, []);
	useEffect(() => {
		const {
			top: containerTop = 0,
			left: containerLeft = 0,
			height: containerHeight = 0,
			width: containerWidth = 0,
			bottom: containerBottom = 0
		} = toolTipContainerRef.current.getBoundingClientRect() || {};

		const {
			top: tooltipTop = 0,
			left: tooltipLeft = 0,
			height: tooltipHeight = 0,
			width: tooltipWidth = 0
		} = toolTipRef.current?.getBoundingClientRect() || {};
		if (tooltipDirection === "top") {
			console.log('top', containerHeight + tooltipHeight - 9);
			setTooltipPosition({ top: containerHeight - tooltipHeight + 9 });
		} else {
			console.log('bottom', containerHeight + 9, '////////////');
			setTooltipPosition({ top: containerHeight + 9});
		}
	},[active]);
	useEffect(() => {
		// if(hideOnClickEscKey) {
		const handleKeyUp = (e: any) => {
			console.log(toolTipContainerRef.current.getBoundingClientRect());
			console.log(toolTipRef.current?.getBoundingClientRect());
			const {
				top: containerTop = 0,
				left: containerLeft = 0,
				height: containerHeight = 0,
				width: containerWidth = 0,
				bottom: containerBottom = 0
			} = toolTipContainerRef.current.getBoundingClientRect() || {};

			const {
				top: tooltipTop = 0,
				left: tooltipLeft = 0,
				height: tooltipHeight = 0,
				width: tooltipWidth = 0
			} = toolTipRef.current?.getBoundingClientRect() || {};
			if (
				(containerTop < 0 || containerBottom < 0) &&
				(containerTop - containerHeight < 0 || containerBottom - containerWidth < 0)
			) {
				// console.log(123);
				setActive(false);
			}
			// console.log(top, "========", left);
			
			if (tooltipDirection === "top") {
				setTooltipPosition({ top: containerHeight + tooltipHeight - 9 });
			} else {
				console.log(containerHeight + 9, '////////////');
				setTooltipPosition({ top: containerHeight + 9});
			}
		};
		window.document.addEventListener("scroll", handleKeyUp);
		return () => window.document.removeEventListener("scroll", handleKeyUp);
		// }
	}, []);

	const showTip = () => {
		setActive(true);
		// timeout = setTimeout(() => {
		// }, 1000);
	};

	const hideTip = () => {
		clearInterval(timeout);
		// setActive(false);
	};

	const findArrowPosition = () => {
		switch (position) {
			case "top":
				return "bottom";
			case "left":
				return "right";
			case "bottom":
				return "top";
			case "right":
				return "left";
		}
	};
	// console.log(toolTipContainerRef);
	return (
		<div>
			<TooltipContainer
				ref={toolTipContainerRef}
				className={`tooltip__container ${addedClassName} tooltip__${tooltipDirection} tooltip_arrow__${findArrowPosition()}`}
				onClick={() => {
					showTip();
				}}
				onMouseEnter={() => {
					showTip();
				}}
				onMouseLeave={() => {
					hideTip();
				}}
			>
				{children}
				{active && (
					<div
						ref={toolTipRef}
						className={`tooltip__tip tooltip__${position}`}
						style={tooltipPosition}
					>
						{content}
					</div>
				)}
			</TooltipContainer>
		</div>
	);
};

export default Tooltip;
